import React from 'react';
import { FORM_TYPES } from '../../utils/constants';

import RadioElement from './RadioElement';
import SelectElement from './SelectElement';
import TextInputElement from './TextInputElement';
import CheckBoxElement from './CheckBoxElement';

const FormElements = data => {
  switch (data.type) {
    case FORM_TYPES.select:
      return <SelectElement data={data} />;
    case FORM_TYPES.checkbox:
      return <CheckBoxElement data={data} />;
    case FORM_TYPES.radio:
      return <RadioElement data={data} />;
    default:
      return <TextInputElement data={data} />;
  }
};

export default FormElements;
