import React from 'react';
import { Switch } from 'react-router-dom';

import AppRoute from './AppRoute';
import Home from '../containers/Home';
import Survey from '../containers/Survey';
import SurveysList from '../containers/SurveysList';
import Login from '../containers/Login';
import Booking from '../containers/Booking';
import Profile from '../containers/Profile';

export default function Routes() {
  return (
    <Switch>
      <AppRoute path="/" exact component={Home} />
      <AppRoute path="/login" exact component={Login} header="login" />
      <AppRoute path="/survey" exact component={Survey} header="survey" />
      <AppRoute path="/surveys-list" exact component={SurveysList} />
      <AppRoute path="/booking" exact component={Booking} header="calendar" />
      <AppRoute path="/profile" exact component={Profile} header="profile" />
    </Switch>
  );
}
