import React from 'react';
import { Control } from 'react-redux-form';
export default function CheckBoxElement({ data }) {
  return (
    <>
      <label htmlFor={data.elementId}>{data.name}:</label>
      <div className="form-control">
        {data.choices.map((x, i) => (
          <React.Fragment key={i}>
            <label htmlFor={`${data.elementId}-${x}`}>{x}</label>
            <Control.checkbox
              model={data.elementId}
              id={`${data.elementId}-${x}`}
            />
          </React.Fragment>
        ))}
      </div>
    </>
  );
}
