import React from 'react';
import { Control } from 'react-redux-form';

import './../../assets/styles/components/form/FormBasics.scss';

export default function TextInputElement({ data }) {
  return (
    <>
      <label className="form--label" htmlFor={data.elementId}>
        {data.name}:
      </label>
      <div className="form-control form-control--input">
        <Control.text
          model={data.elementId}
          id={data.elementId}
          placeholder={data.name}
          className="form--input"
        />
      </div>
    </>
  );
}
