import React from 'react';
import { Route } from 'react-router-dom';
import { Layout as BaseLayout } from '../components/Layout';

export default function AppRoute({
  component: Component,
  header = 'home',
  layout: Layout = props => (
    <BaseLayout header={header}>{props.children}</BaseLayout>
  ),
  ...rest
}) {
  return (
    <Route
      {...rest}
      render={props => (
        <Layout>
          <Component {...props} />
        </Layout>
      )}
    />
  );
}
