import React from 'react';
import './../assets/styles/components/Button.scss';

const Button = ({
  text,
  type,
  uppercase,
  className,
  onClick = undefined,
  disabled = false
}) => {
  if (uppercase) {
    text = text.toUpperCase();
  }
  return (
    <button
      type={type}
      className={'button ' + className}
      onClick={onClick}
      disabled={disabled}
    >
      {text}
    </button>
  );
};

export default Button;
