import React from 'react';
import { Control } from 'react-redux-form';

import './../../assets/styles/components/form/FormBasics.scss';
import './../../assets/styles/components/form/RadioElement.scss';

export default function RadioElement({ data }) {
  return (
    <>
      <label htmlFor={data.elementId}>{data.name}:</label>
      <div className="form-control form-control--radio">
        {data.choices.map((x, i) => (
          <React.Fragment key={i}>
            <Control.radio
              className="form__radio"
              model={data.elementId}
              value={x}
              id={`${data.elementId}-${x}`}
            />
            <label className="form__label" htmlFor={`${data.elementId}-${x}`}>
              {x}
            </label>
          </React.Fragment>
        ))}
      </div>
    </>
  );
}
