import axios from 'axios';
import querystring from 'querystring';

const BaseUrl = 'http://172.24.0.218:3002/api/v1';

let config = {
  headers: { 'Content-Type': 'application/json' }
};

const setAuthorizationToken = (axios, token) => {
  if (token) {
    axios.defaults.headers.common.Authorization = token;
  } else {
    delete axios.defaults.headers.common.Authorization;
  }
};

class ApiClient {
  // Get Method API call
  static get(url, params, token = '') {
    return new Promise((resolve, reject) => {
      setAuthorizationToken(axios, token);
      axios
        .get(`${BaseUrl}/${url}?${querystring.stringify(params)}`, config)
        .then(result => {
          resolve(result);
        })
        .catch(err => {
          reject(err);
        });
    });
  }
  // Post Method API call
  static post(url, params, token = '') {
    return new Promise((resolve, reject) => {
      setAuthorizationToken(axios, token);
      axios
        .post(`${BaseUrl}/${url}`, params, config)
        .then(result => {
          resolve(result);
        })
        .catch(err => {
          reject(err);
        });
    });
  }
  // Put Method API call
  static put(url, params, token = '') {
    return new Promise((resolve, reject) => {
      setAuthorizationToken(axios, token);
      axios
        .put(`${BaseUrl}/${url}`, params, config)
        .then(result => {
          resolve(result);
        })
        .catch(err => {
          reject(err);
        });
    });
  }
}

export default ApiClient;
