import React from 'react';
import Header from './Header';
import ProgressBar from './ProgressBar';
import UserHeader from './UserHeader';

export const Layout = ({ children, header }) => {
  return (
    <>
      {header !== 'login' && (
        <Header header={header}>
          {header === 'home' && (
            <p className="text-center header__paragraph">Hello, User</p>
          )}
          {header === 'survey' && <ProgressBar />}
          {header === 'calendar' && (
            <p className="text-center header__paragraph">Interview Calendar</p>
          )}
          {header === 'profile' && <UserHeader />}
        </Header>
      )}
      {children}
    </>
  );
};

