import ApiClient from '../config/apiClient';
import { replaceSpace } from '../utils/common';
import { SURVEY, SET_PAGE_NUMBER } from './actionTypes';

export const getSurveyAction = data => ({ type: SURVEY, data: data });
export const setPageNumber = page => ({ type: SET_PAGE_NUMBER, page });

/*************** save user survey *************/
export const saveUserSurvey = (params = {}) => async dispatch => {
  const result = await ApiClient.post('survey/save', params);
  if (result.data && result.data.statusCode === 200) {
    console.log('saved');
  }
};
/*************** Get survey questions *************/
export const getSurvey = (params = {}) => async dispatch => {
  const result = await ApiClient.get('survey/questions', params);
  if (result.data && result.data.statusCode === 200) {
    const elements = result.data.data.elements.map(x => {
      x.refName && (x.refId = replaceSpace(x.refName));
      x.elementId = '.' + replaceSpace(x.name);
      return x;
    });
    dispatch(getSurveyAction({ ...result.data.data, elements }));
  }
};
