import React from 'react';

import '../assets/styles/components/DashboardButton.scss';

const DashboardButton = ({ text, icon, alt, gotoRoute, route }) => {
  return (
    <div>
      <button
        className="bgc-primary--light btn"
        onClick={() => gotoRoute(route)}
      >
        <img src={icon} alt={alt} />
      </button>
      <p className="text-center">{text}</p>
    </div>
  );
};

export default DashboardButton;
