import React from 'react';
import { Control } from 'react-redux-form';

import './../../assets/styles/components/form/FormBasics.scss';

export default function SelectElement({ data }) {
  return (
    <>
      <label htmlFor={data.elementId}>{data.name}:</label>
      <div className="form-control form-control--select">
        <Control.select model={data.elementId} id={data.elementId}>
          <option>Select</option>
          {data.choices.map((x, i) => (
            <option key={i} value={x}>
              {x}
            </option>
          ))}
        </Control.select>
      </div>
    </>
  );
}
