const survey = (state = initialState, action) => {
  switch (action.type) {
    case SURVEY:
      return { ...state, ...action.data };
    case SET_PAGE_NUMBER:
      return { ...state, pageNumber: action.page };
    default:
      return state;
  }
};

export default survey;
