import React, { useEffect, useState, Fragment, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { LocalForm } from 'react-redux-form';
import FormElements from '../components/form/index';
import Footer from '../components/Footer';
import Button from '../components/Button';
import '../assets/styles/components/Survey.scss';
import { getSurvey, setPageNumber, saveUserSurvey } from '../actions/survey';

const Survey = () => {
  const dispatch = useDispatch();
  const { survey } = useSelector(state => state);
  const { pageNumber, total } = survey;
  const [formData, setFormData] = useState({});
  const [formValues, setFormValues] = useState([]);

  useEffect(() => {
    fetchSurvey(pageNumber);
  }, [pageNumber]);

  /************ Get survey list ************/
  const fetchSurvey = useCallback(
    page => {
      dispatch(
        getSurvey({
          surveyId: '5e5fa100fed30e183622bab2',
          type: 2,
          pageNumber: page
        })
      );
    },
    [pageNumber]
  );

  /************* Handle Page change **********/
  const handlePageChange = page => {
    dispatch(setPageNumber(page));
    fetchSurvey(page);
  };
  /************ Save Survey ************/
  const saveSurvey = useCallback(
    values => {
      const elements = survey.elements.map(element => {
        const key = element.elementId.split('.');
        return { _id: element._id, value: values[key[1]] || '' };
      });
      dispatch(
        saveUserSurvey({
          surveyId: survey.surveyId,
          questionId: survey._id,
          elements
        })
      );
    },
    [pageNumber]
  );
  // console.log('survey.elements', JSON.stringify(survey.elements));
  return (
    <>
      <LocalForm
        onChange={values => {
          setFormData({ ...values });
        }}
        onSubmit={values => saveSurvey(values)}
      >
        <div className="container survey-container">
          <h1 className="text-center">{survey.pageName}</h1>
          <p>
            Instructions: Thank you for completing this customer profile survey.
            This is an important tool in strategic planning for the sterile
            rollout transition and ensuring an ongoing competitive position in
            Europe. Several sections of this survey may require customer
            stakeholder engagement to ensure accuracy in answers. Where
            indicated, please provide the names of stakeholders with whom you
            have engaged.
          </p>
          {survey.elements &&
            survey.elements.map((x, i) => {
              if (x.refName) {
                return (
                  formData[x.refId] &&
                  formData[x.refId] === x.refValue && (
                    <Fragment key={i}>
                      <div className="form-group">
                        <FormElements {...x} />
                      </div>
                    </Fragment>
                  )
                );
              } else {
                return (
                  <Fragment key={i}>
                    <div className="form-group">
                      <FormElements {...x} />
                    </div>
                  </Fragment>
                );
              }
            })}
        </div>
        <Footer>
          <Button
            text="Back"
            type="submit"
            className="button--default"
            onClick={() => handlePageChange(pageNumber - 1)}
            uppercase
            disabled={pageNumber === 1}
          />
          <Button
            text="Next"
            type="submit"
            className="button--default"
            onClick={() => handlePageChange(pageNumber + 1)}
            uppercase
            disabled={pageNumber === total}
          />
        </Footer>
      </LocalForm>
    </>
  );
};

export default Survey;
